/**
 * 文件用途说明 : 连接器的实现抽象层：默认支持XMLHttpRequest
 * 以后还可以扩展不同的实现，例如jsonp、node
 * @Client
 *
 * 项目名称 : dfzq-obboot-mobi
 * 作者姓名 : crixusshen
 * 创建日期 : 16/3/9
 * 修改日期 : 16/3/9
 * 版权所有 : 东方证券股份有限公司
 **/
(function (define) {
    'use strict';

    var undef;

    // http-default
    define(function (require) {

        /**
         * HTTP request
         *
         * @field {string} [method='GET'] HTTP method, commonly GET, POST, PUT, DELETE or HEAD
         * @field {string|UrlBuilder} [path=''] path template with optional path variables
         * @field {Object} [params] parameters for the path template and query string
         * @field {Object} [headers] custom HTTP headers to send, in addition to the clients default headers
         * @field [entity] the HTTP entity, common for POST or PUT requests
         * @field {boolean} [canceled] true if the request has been canceled, set by the client
         * @field {Function} [cancel] cancels the request if invoked, provided by the client
         * @field {Client} [originator] the client that first handled this request, provided by the interceptor
         *
         * @class Request
         */

        /**
         * HTTP response
         *
         * @field {Object} [request] the request object as received by the root client
         * @field {Object} [raw] the underlying request object, like XmlHttpRequest in a browser
         * @field {number} [status.code] status code of the response (i.e. 200, 404)
         * @field {string} [status.text] status phrase of the response
         * @field {Object] [headers] response headers hash of normalized name, value pairs
         * @field [entity] the response body
         *
         * @class Response
         */

        /**
         * HTTP client
         *
         * @field {function} 为默认连接器的function添加拦截器
         *
         * @param {Request} HTTP请求
         * @returns {ResponsePromise<Response>} 一个promise处理HTTP响应
         *
         * @class Client
         */

        /**
         * 对请求器进行promise，使逻辑异步
         * Extended when.js
         *
         * @method entity promise for the HTTP entity
         * @method status promise for the HTTP status code
         * @method headers promise for the HTTP response headers
         * @method header promise for a specific HTTP response header
         *
         * @class ResponsePromise
         * @extends Promise
         */

        var client, target, platformDefault;

        // 所有连接器的基类
        client = require('./client');

        /**
         * 做一次默认连接器的请求
         * @param {Request} HTTP请求
         * @returns {Promise<Response>} 一个promise处理HTTP响应
         */
        function defaultClient() {
            // target是一个默认连接器的实现类
            return target.apply(undef, arguments);
        }

        /**
         * 改变默认连接器
         * @param {Client} 新的默认连接器
         */
        defaultClient.setDefaultClient = function setDefaultClient(client) {
            target = client;
        };

        /**
         * 获取当前默认连接器的引用
         * @returns {Client} 默认连接器
         */
        defaultClient.getDefaultClient = function getDefaultClient() {
            return target;
        };

        /**
         * 重置默认连接器
         */
        defaultClient.resetDefaultClient = function resetDefaultClient() {
            target = platformDefault;
        };

        /**
         * 设置当前平台的默认连接器
         */
        defaultClient.setPlatformDefaultClient = function setPlatformDefaultClient(client) {
            if (platformDefault) {
                throw new Error('Unable to redefine platformDefaultClient');
            }
            target = platformDefault = client;
        };

        return client(defaultClient);

    });

}(
    typeof define === 'function' && define.amd ? define : function (factory) {
        module.exports = factory(require);
    }
    // Boilerplate for AMD and Node
));


