(function (define) {

    define(function (require) {

        var interceptor = require('../interceptor');
        var when = require('../../when/promise');

        return interceptor({
            init: function (config) {
                // 默认超时模式没有设置为0
                config.timeout = config.timeout || 0;
                // 默认短暂链接为false
                config.transient = !!config.transient;
                return config;
            },
            request: function (request, config) {
                // 构建timeout, transient和abortTrigger属性，其中abortTrigger是一个可能被放弃执行的xhr钩子
                var timeout, abortTrigger, transient;
                timeout = 'timeout' in request ? request.timeout : config.timeout;
                transient = 'transient' in request ? request.transient : config.transient;
                // 如果超时属性设置为负整数，则直接返回request
                if (timeout <= 0) {
                    return request;
                }

                // 构建超时（允许放弃请求）xhr钩子延时对象
                var self = this;
                abortTrigger = when.promise(function (resolve, reject) {
                    self.timeout = setTimeout(function () {
                        // 一旦xhr超时则将request返回给reject并标识是一个超时原因
                        reject({ request: request, error: 'timeout' });
                        // 断开连接
                        if (request.cancel) {
                            request.cancel();
                            if (transient) {
                                request.canceled = false;
                            }
                        }
                        else if (!transient) {
                            request.canceled = true;
                        }
                    }, timeout);
                });

                return new interceptor.ComplexRequest({ request: request, abort: abortTrigger });
            },
            response: function (response) {
                // 一旦有服务端响应，清除包装类中已定义的超时器
                if (this.timeout) {
                    clearTimeout(this.timeout);
                    delete this.timeout;
                }
                return response;
            }
        });
    });

}(
    typeof define === 'function' && define.amd ? define : function (factory) {
        module.exports = factory(require);
    }
    // Boilerplate for AMD and Node
));


