import http from "./client/base/default"
import xhr from "./client/xhr"
const mimeHandler = require('./interceptor/mimeHandler')
const errorcodeHandler = require('./interceptor/errorcodeHandler')
const timeoutHandler = require('./interceptor/timeoutHandler')
const interceptor = require('./interceptor');

const httpClient = _ => {
    http.setPlatformDefaultClient(xhr)
    return http
}

export {
    httpClient,
    mimeHandler,
    errorcodeHandler,
    timeoutHandler,
    interceptor
}


