(function (define) {
    'use strict';

    define(function (require) {

        var when = require('../../when/promise'),
            normalizeHeaderName = require('./normalizeHeaderName');

        function property(promise, name) {
            return promise.then(
                function (value) {
                    return value && value[name];
                },
                function (value) {
                    return when.reject(value && value[name]);
                }
            );
        }

        function entity() {
            /*jshint validthis:true */
            return property(this, 'entity');
        }

        function status() {
            /*jshint validthis:true */
            return property(property(this, 'status'), 'code');
        }

        function headers() {
            /*jshint validthis:true */
            return property(this, 'headers');
        }

        function header(headerName) {
            /*jshint validthis:true */
            headerName = normalizeHeaderName(headerName);
            return property(this.headers(), headerName);
        }

        function follow(rels) {
            /*jshint validthis:true */
            rels = [].concat(rels);
            return make(when.reduce(rels, function (response, rel) {
                if (typeof rel === 'string') {
                    rel = { rel: rel };
                }
                if (typeof response.entity.clientFor !== 'function') {
                    throw new Error('Hypermedia response expected');
                }
                var client = response.entity.clientFor(rel.rel);
                return client({ params: rel.params });
            }, this));
        }

        function make(promise) {
            promise.status = status;
            promise.headers = headers;
            promise.header = header;
            promise.entity = entity;
            promise.follow = follow;
            return promise;
        }

        function responsePromise() {
            // 2016-04-03 原来依赖于when.js的语法。目前when由自己来实现
            //return make(when.apply(when, arguments));

            var args = arguments;
            return when.promise(function (resolved, rejected) {
                try{
                    var res = args[0];
                    resolved(res);
                }catch(ex){
                    rejected(ex);
                }
            }).then(
                args[1]
            ).otherwise(
                args[2]
            );
        }

        responsePromise.make = make;
        responsePromise.reject = function (val) {
            return make(when.reject(val));
        };
        responsePromise.promise = function (func) {
            return make(when.promise(func));
        };

        return responsePromise;

    });

}(
    typeof define === 'function' && define.amd ? define : function (factory) {
        module.exports = factory(require);
    }
    // Boilerplate for AMD and Node
));


